<template>
    <div>
        <div style="border-bottom: 1px solid #eee;padding: 10px;margin-bottom: 10px;"></div>
        <el-form :inline="true" :model="searchParam" class="search-form-box">
            <el-form-item label="">
                <el-select v-model="searchParam.type" placeholder="">
                    <el-option label="" :value="1"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="searchParam.keyword" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click=""></el-button>
            </el-form-item>
        </el-form>
        <div style="padding: 10px;">
            <el-table :data="searchResultList" border style="width: 100%; margin-bottom: 5px;" max-height="500">
                <el-table-column prop="id" label="" width="60"></el-table-column>
                <el-table-column prop="name" label=""></el-table-column>
                <el-table-column prop="spaceExplain" label="CODE"></el-table-column>
                <el-table-column prop="createTime" label=""></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button size="small" plain type="primary" v-on:click="editUserInfo(scope.row)"></el-button>
                        <el-button size="small" plain type="primary" v-on:click="resetPassword(scope.row)"></el-button>
                        <el-button size="small" plain type="warning" v-on:click="editUserInfo(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="page-info-box">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :page-sizes="[20, 50, 100]"
                    :page-size="20"
                    :current-page="searchParam.pageNum"
                    layout="prev, pager, next, jumper, sizes, total"
                    :total="totalCount"
            >
            </el-pagination>
        </div>
        <!---->
        <el-dialog title="" :visible.sync="editUserDialogVisible" width="600px">
            <el-form ref="form" :model="editUserForm" label-width="80px">
                <el-form-item label="">
                    <el-input v-model="editUserForm.name"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editUserForm.name"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editUserForm.name"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editUserForm.name"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-radio-group v-model="editUserForm.resource">
                        <el-radio label=""></el-radio>
                        <el-radio label=""></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="editUserForm.xx" multiple filterable placeholder="">
                        <el-option v-for="item in roleOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click=""></el-button>
                    <el-button></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
    var app;

    export default {
        data() {
            return {
                editUserDialogVisible: false,
                totalCount: 0,
                searchParam: {
                    type: 1,
                    keyword: '',
                    pageSize: 20,
                    pageNum: 1,
                },
                searchResultList: [
                    {name: ''}
                ],
                roleOptions: [
                    {value: ''}
                ],
                editUserForm: {},
            };
        },
        mounted: function () {
            app = this;
        },
        methods: {
            handleSizeChange(val) {
                this.searchParam.pageSize = val;
            },
            handleCurrentChange(val) {
                this.searchParam.pageNum = val;
            },
            editUserInfo() {
                this.editUserDialogVisible = true;
            },
            resetPassword() {
            },
        }
    }

</script>
<style>
    .search-form-box{padding: 10px;}
    .page-info-box{text-align: right;margin: 20px 0 50px 0;}
</style>




