<template>
    <div class="user-list-vue">
        <el-breadcrumb separator-class="el-icon-arrow-right" style="padding: 20px 10px;">
            <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-form :inline="true" :model="searchParam" class="search-form-box">
            <el-form-item label="">
                <el-select v-model="searchParam.type" placeholder="">
                    <el-option label="ID" :value="1"></el-option>
                    <el-option label="" :value="2"></el-option>
                    <el-option label="" :value="3"></el-option>
                    <el-option label="" :value="4"></el-option>
                    <el-option label="" :value="5"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="searchParam.keyword" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="getUserList"></el-button>
            </el-form-item>
            <el-form-item>
                <el-button type="success" @click="addUserInfo"><i class="el-icon-plus"></i> </el-button>
            </el-form-item>
        </el-form>
        <div style="padding: 10px;" v-loading="searchLoading">
            <el-table :data="searchResultList" border style="width: 100%; margin-bottom: 5px;" max-height="500">
                <el-table-column prop="id" label="" width="60"></el-table-column>
                <el-table-column prop="userNo" label=""></el-table-column>
                <el-table-column prop="email" label=""></el-table-column>
                <el-table-column prop="userName" label=""></el-table-column>
                <el-table-column prop="phone" label=""></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">{{scope.row.sex==0?'':''}}</template>
                </el-table-column>
                <el-table-column prop="creationTime" label=""></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">{{scope.row.delFlag==0?'':''}}</template>
                </el-table-column>
                <el-table-column label="" width="300">
                    <template slot-scope="scope">
                        <el-button size="mini" plain type="primary" v-on:click="editUserInfo(scope.row)"></el-button>
                        <el-button size="mini" plain type="success" v-on:click="editUserAuthFun(scope.row)"></el-button>
                        <el-button size="mini" plain type="warning" v-on:click="resetPassword(scope.row)"></el-button>
                        <el-button size="mini" plain type="danger" v-on:click="deleteUser(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="page-info-box">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :page-sizes="[20, 50, 100]"
                    :page-size="20"
                    :current-page="searchParam.pageNum"
                    layout="prev, pager, next, jumper, sizes, total"
                    :total="totalCount"
            >
            </el-pagination>
        </div>
        <!---->
        <el-dialog title="" :visible.sync="editUserAuthDialogVisible" width="600px">
            <el-form ref="form" label-width="80px">
                <el-form-item label="">
                    <el-input v-model="editUserForm.userNo" disabled></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editUserForm.userName" disabled></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="editUserAuth" multiple filterable placeholder="" style="width: 100%;">
                        <el-option v-for="item in allUserAuth" :key="item.id" :label="item.authDesc" :value="item.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="editUserAuthSave"></el-button>
                    <el-button @click="editUserAuthDialogVisible = false"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <!---->
        <el-dialog :title="editUserForm.id > 0 ? '':''" :visible.sync="editUserDialogVisible" width="600px">
            <el-form ref="form" :model="editUserForm" label-width="80px">
                <el-form-item label="">
                    <el-input v-model="editUserForm.userNo"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editUserForm.userName"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editUserForm.phone"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editUserForm.email"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-switch v-model="editUserForm.delFlag" :active-value="0" active-text="" :inactive-value="2" inactive-text=""></el-switch>
                </el-form-item>
                <el-form-item label="">
                    <el-radio-group v-model="editUserForm.sex">
                        <el-radio :label="1"></el-radio>
                        <el-radio :label="0"></el-radio>
                    </el-radio-group>
                </el-form-item>
<!--                -->
<!--                <el-form-item label="">-->
<!--                    <el-select v-model="editUserForm.role" multiple filterable placeholder="">-->
<!--                        <el-option v-for="item in roleOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>-->
<!--                    </el-select>-->
<!--                </el-form-item>-->
                <el-form-item>
                    <el-button type="primary" @click="updateEditUser"></el-button>
                    <el-button @click="editUserDialogVisible = false"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
    import toast from '../../common/lib/common/toast'
    var app;

    export default {
        data() {
            return {
                searchLoading: false,
                editUserDialogVisible: false,
                editUserAuthDialogVisible: false,
                totalCount: 0,
                searchParam: {
                    type: 1,
                    keyword: '',
                    pageSize: 20,
                    pageNum: 1,
                },
                searchResultList: [],
                roleOptions: [
                    {value: ''}
                ],
                editUserForm: {},
                allUserAuth: [],
                editUserAuth: [],
            };
        },
        mounted: function () {
            app = this;
            this.getUserList();
        },
        methods: {
            handleSizeChange(val) {
                this.searchParam.pageSize = val;
                this.getUserList();
            },
            handleCurrentChange(val) {
                this.searchParam.pageNum = val;
                this.getUserList();
            },
            editUserAuthFun(row) {
                app.allUserAuth = [];
                app.editUserAuth = [];
                var param = {userIds: row.id};
                this.common.post(this.apilist1.userAuthList, param, function (json) {
                    app.editUserAuth = [];
                    app.allUserAuth = json.data;
                    app.editUserAuthDialogVisible = true;
                    app.editUserForm = JSON.parse(JSON.stringify(row));
                    for (var i = 0; i < app.allUserAuth.length; i++) {
                        if (app.allUserAuth[i].checked == 1) {
                            app.editUserAuth.push(app.allUserAuth[i].id);
                        }
                    }
                });
            },
            editUserAuthSave() {
                var param = {
                    userIds: this.editUserForm.id,
                    authIds: this.editUserAuth.join(","),
                };
                this.common.post(this.apilist1.updateUserAuth, param, function (json) {
                    toast.success("");
                    app.editUserAuthDialogVisible = false;
                });
            },
            editUserInfo(row) {
                app.editUserDialogVisible = true;
                app.editUserForm = JSON.parse(JSON.stringify(row));
            },
            addUserInfo() {
                this.editUserDialogVisible = true;
                this.editUserForm = {};
            },
            resetPassword(row) {
                this.$confirm('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.editUserForm = JSON.parse(JSON.stringify(row));
                    this.common.post(this.apilist1.resetPassword, this.editUserForm, function (json) {
                        app.$confirm("" + json.data).then(()=> {
                            done();
                        }).catch(()=> {});
                    });
                }).catch(()=>{});
            },
            deleteUser(row) {
                this.$confirm('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.editUserForm = JSON.parse(JSON.stringify(row));
                    this.common.post(this.apilist1.deleteUserInfo, this.editUserForm, function (json) {
                        toast.success("");
                        app.getUserList();
                    });
                }).catch(()=>{});
            },
            updateEditUser() {
                this.common.post(this.apilist1.updateUserInfo, this.editUserForm, function (json) {
                    toast.success("");
                    app.editUserDialogVisible = false;
                    app.getUserList();
                });
            },
            getUserList() {
                this.searchLoading = true;
                this.common.post(this.apilist1.getUserInfoList, this.searchParam, function (json) {
                    // 
                    setTimeout(()=>{app.searchLoading = false;}, 500);
                    app.totalCount = json.total;
                    app.searchResultList = json.data;
                });
            },
        }
    }

</script>
<style>
    .user-list-vue .search-form-box{padding: 10px;}
    .user-list-vue .page-info-box{text-align: right;margin: 20px 0 50px 0;}
    .user-list-vue .el-button+.el-button{margin-left: 5px;}
</style>




