import global from '../../config/global'

/**
 * 
 * @author
 * @since 201757
 */
export default {
	notOpen: function () {
		global.vue.$message({
			message: '',
			type: 'warning',
			showClose: true
		});
	},
	success: function (msg, time) {
		global.vue.$message({
			message: msg,
			duration: time || 3000,
			type: 'success',
			showClose: true
		});
	},
	warn: function (msg, time) {
		global.vue.$message({
			message: msg,
			duration: time || 3000,
			type: 'warning',
			showClose: true
		});
	},
	error: function (msg, time) {
		global.vue.$message({
			message: msg,
			duration: time || 3000,
			type: 'error',
			showClose: true
		});
	},
};



